body {
  background-color: aliceblue !important;
  min-height: 100%;
}

.buttons {
  margin: 5vh;
  display: flex;
  justify-content: space-around;
}

.buttons > button {
  outline: none;
  font-size: 1.25em;
  font-weight: 700;
  color: white;
  background-color: #673ab7;
  display: inline-block;
  padding: 1vh;
  border-radius: 10px;
  border: none;
}

.buttons > a {
  outline: none;
  font-size: 1.25em;
  font-weight: 700;
  color: white;
  background-color: #673ab7;
  display: inline-block;
  padding: 1vh;
  border-radius: 10px;
  border: none;
  text-decoration: none;
}

.buttons > a:hover {
  text-decoration: none;
  color: white !important;
  cursor: pointer;
}
.inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.inputfile + label {
  font-size: 1.25em;
  font-weight: 700;
  color: white;
  background-color: #673ab7;
  display: inline-block;
  padding: 1vh;
  border-radius: 10px;
}

.inputfile:focus + label,
.inputfile + label:hover {
  cursor: pointer;
}

.added_panels {
  margin: 0 25% 25% 25%;
  width: 50%;
}

.added_panels > * > * {
  margin-top: 5vh;
}

.added_panel {
  padding: 5vh;
  background-color: white;
  border-radius: 10px;
  box-shadow: 1px 1px 7px 0px;
}

.added_panel > p > span {
  font-style: italic;
  text-decoration: underline;
  color: #999;
  font-size: 2vh;
  cursor: pointer;
  float: right;
}

input[type="text"] {
  outline: none !important;
  width: 100%;
  padding: 1vh;
  border: 1px solid #999999;
  border-radius: 10px;
}

.add_button {
  position: fixed;
  right: 15vw;
  top: 40%;
}

.radio_options {
  display: block;
  margin-top: 1vh;
}

.radio_options > div {
  margin-top: 1vh;
  display: flex;
}

.radio_options > div > span {
  margin-top: 1vh;
}

.radio_add {
  font-size: 3vh;
  margin-left: 2vh;
  font-weight: 800;
  cursor: pointer;
}

.remove_button {
  position: absolute;
  right: 26vw;
  color: #e45d5d;
}

.myforms {
  display: flex;
}

.myforms > * {
  margin: 1vw;
}

textarea {
  width: 100%;
}

.form_conatiner {
  padding: 1% 2% 0% 2%;
  color: white;
  background-color: #673ab7;
  border-radius: 10px;
  box-shadow: 1px 1px 7px 0px;
  cursor: pointer;
}

.radio_distance > * {
  margin: 1vw;
}
.drop_distance {
  display: grid;
}

.drop_distance > select {
  width: 50%;
}

.drop_distance > * {
  margin: 1vw;
}
